'use client';

import { useEffect } from 'react';
import { useProfile } from '@/contexts/ProfileContext';
import { ProfileType } from '@/types/profile';

interface ProfileLayoutWrapperProps {
  children: React.ReactNode;
  profile: ProfileType;
  userName?: string;
  userInitials?: string;
}

export default function ProfileLayoutWrapper({
  children,
  profile,
  userName,
  userInitials,
}: ProfileLayoutWrapperProps) {
  const { setProfile, setUserName, setUserInitials } = useProfile();

  useEffect(() => {
    setProfile(profile);
    if (userName) setUserName(userName);
    if (userInitials) setUserInitials(userInitials);
  }, [profile, userName, userInitials, setProfile, setUserName, setUserInitials]);

  return <>{children}</>;
}

