'use client';

import { NavigationProvider } from '@/contexts/NavigationContext';
import { FinanzasProvider } from '@/contexts/FinanzasContext';
import { HomeProvider } from '@/contexts/HomeContext';
import { UsuariosProvider } from '@/contexts/UsuariosContext';
import ProfileLayoutWrapper from '@/components/layout/ProfileLayoutWrapper';

export default function AdminLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <ProfileLayoutWrapper
      profile="administrador"
      userName="Administrador"
      userInitials="AD"
    >
      <NavigationProvider defaultActiveItem="home">
        <FinanzasProvider>
          <HomeProvider>
            <UsuariosProvider>
              {children}
            </UsuariosProvider>
          </HomeProvider>
        </FinanzasProvider>
      </NavigationProvider>
    </ProfileLayoutWrapper>
  );
}

